.topdiv {
  color: gold;
  top: 0px; /* aligns the header to the top of the page */
  width: 100%; /* spans the entire width of the page */
  height: 45px; /* sets the height of the header */
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.displaydiv {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
}

.viewerdiv {
  border: 2px skyblue solid;
  background-color: black;
  display: flex;
  flex-wrap: row; 
  margin: auto;  
  width: 40%; /* spans the entire width of the page */
  height: 10vh; /* sets the height of the header */
  align-items: center;
  justify-content: center;
  font-family: 'NexaHeavy';
  opacity: 0.65;
}

.buttondiv {
  margin: auto;  
  width: 40%; /* spans the entire width of the page */
  height: 15vh; /* sets the height of the header */
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 4px;
  padding-right: 6px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.maindiv {
  border: 2px skyblue solid;
  background-color: black;
  margin: auto;  
  width: 40%; /* spans the entire width of the page */
  height: 25vh; /* sets the height of the header */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  opacity: 0.6;
}

.lowerdiv {  
  display: flex;
  margin: auto;  
  width: 40%; /* spans the entire width of the page */
  height: 25vh; /* sets the height of the header */
  align-items: top;
  justify-content: center;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.centered {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 9999;
  background-color: black;
  border: 2px lime solid;
  text-align: center;
  margin: 2px;
  width: 40%;
  justify-content: center;
  align-items: center;
  border-radius: 45px;
  opacity: 0.8;
  padding-bottom: 20px;
  padding-top: 5px;
  font-size: 30px;
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.display-contain {
  background-color: green;
  border: 1px solid black;
  border-radius: 5px;
  height: 80%;
  width: 8%;
  margin: 2px;
  padding: 2px;
  justify-content: center;
  text-align: center;
  }

  .tinytext{
    font-size: 16px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color: white;
    padding: 4px;
    margin: 4px;
  }

  .smalltext{
    font-size: 22px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color: skyblue;
    padding: 4px;
    margin: 4px;
  }

  .ultratext{
    font-size: 10px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color: white;
    padding: 4px;
    margin: 4px;
  }

  .button-design {
    width: 45%;
    margin: 20px;
    border-radius: 10px;
    height: 30%;
    color: white;
    cursor: pointer;
  }

  .button-2-design {
    width: 45%;
    border-radius: 10px;
    color: white;
    cursor: pointer;
    height: 20%;
    margin: 5%;
  }

  .button-3-design {
    width: 20%;
    color: white;
    cursor: pointer;
    height: 20%;
    border-radius: 5px;
    border: 1px solid green;
    font-weight: bold;
    padding: 2px;
  }

  .bgreen {
    background-color: limegreen;
  }

  .bgray {
    background-color: gray;
  }

  .bskyblue {
    background-color: skyblue;
  }

  .xbo-button {
    width: 100%;
    padding: 8px;
    background-color: green;
    color: white;
    border: 1px solid lime;
    border-radius: 4px;
    cursor: pointer;
    width: 50%;
  }
  .xbo-max {
    background-color: white;
    color: green;
    background-color: yellow;
    border: 1px solid lime;
    border-radius: 4px;
    cursor: pointer;
    width: 20%;
    height: 100%;
    font-weight: bold;
  }

  .aperture {
    height: 100%;
    width: 80%;
    border: 1px solid lime;
    text-align: center;
  }

  .brskyblue {
    border: 1px solid skyblue;
  }

  .alignfc {
    justify-content: space-between;
    height: 30px;
    margin: 8px;
  }

  .innerdiv {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
  }

  .texten {
    font-weight: bold;
    color: rgb(255, 255, 255, 1)
  }
  
  .whiteline{
    width: 100%;
    border: 1px solid skyblue;
    opacity: 0.3;
  }
