/* Set the style for the scrollbar */
::-webkit-scrollbar {
  width: 10px;
}

/* Set the style for the thumb of the scrollbar */
::-webkit-scrollbar-thumb {
  background-color: black;
  border-radius: 10px;
  border: 1px gold solid;
}

.header {
  color: gold;
  background-color: black;
  position: fixed; /* this makes the header stay at the top of the page */
  top: 0px; /* aligns the header to the top of the page */
  width: 100%; /* spans the entire width of the page */
  height: 45px; /* sets the height of the header */
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 4px;
  padding-right: 6px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  opacity: 0.65;
}

/* style for the first division */
.division1 {
    position: absolute; /* this positions the division beneath the header */
    background-color: transparent;
    background-size: cover;
    background-position: center;
    left: 0; /* aligns the division to the left of the page */
    top: 45px; /* positions the division 50px beneath the header */
    width: 100%; /* spans a third of the width of the page */
    height: calc(100% - 45px); /* spans the remaining length of the page */
    display: flex;
    flex-direction: column;
}

.centered {  
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

/* style for the second division */
.division2 {
  background-color: transparent;
  position: absolute; /* this positions the division beneath the header */
  top: 50px; /* positions the division 50px beneath the header */
  left: 30%; /* aligns the division to the right of the first division */
  height: calc(100% - 45px); /* spans the remaining length of the page */
}

/* style for the third division */
.division3 {
  background-color: transparent;
  position: absolute; /* this positions the division beneath the header */
  top: 45px; /* positions the division 50px beneath the header */
  right: 0px; /* aligns the division to the right of the page */
  width: 50%; /* spans a third of the width of the page */
  height: calc(100% - 45px); /* spans the remaining length of the page */
  background-size: cover;
  background-position: center;
  display: flex;
  flex-direction: column; 
  opacity: 0.85;
}

/* style for the nftloader division */
.nftloader {
  display: flex; 
  flex-direction: row;
  flex-wrap: wrap;
  background-color: black;
  position: absolute; /* this positions the division beneath the header */
  top: 43px; /* positions the division 50px beneath the header */
  left: 39%; /* aligns the division to the left of the page */
  width: 24%; 
  min-height: 100px;
  max-height: calc(100% - 45px);
  margin: 2px; 
  overflow: hidden;
}

.detailLoader {
  display: flex;
  border-radius: 25px;
  flex-direction: column;
  background-color: black;
  position: absolute; /* this positions the division beneath the header */
  top: 43px; /* positions the division 50px beneath the header */
  right: 0%; /* aligns the division to the right of the page */
  width: 35%; /* spans a third of the width of the page */
  max-width: 36%;
  margin: 2px;
  opacity: 0.96;
}

.activitytracker {
  position: absolute; /* this positions the division beneath the header */
  top: 45px; /* positions the division 50px beneath the header */
  left: 24%; /* aligns the division to the right of the page */
  width: 50%; /* spans a third of the width of the page */
  height: calc(100% - 47px); /* spans the remaining length of the page */
  overflow: auto;
  background-size: cover;
  background-position: center;
  display: flex;
  flex-direction: column;
}

