.menu{
  position: absolute;
  display: grid;
  flex-wrap: wrap;
  grid-template-columns: repeat(3, 1fr);
  grid-auto-flow: row dense;  
  grid-auto-rows: 40px;
  grid-row-gap: 0;
  grid-column-gap: 0;
  align-items: center;
  top: 22%;
  left: 1%;
  max-height: 60vh;
  color: goldenrod;
  font-size: medium;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif; 
  opacity: 0.8px;
  grid-column-gap: 0;
  grid-row-gap: 0;
}


.slider {
  position: absolute;
  overflow: hidden;
  top: 8%;
  height: calc(100%-8%);
  left: 1%;
  width: 25%;
}

.slider {
  animation: slide 10s linear infinite;
}

@keyframes slide {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-100%);
  }
}

